.mainBlock {
    padding: 50px 0;
    font-family: 'Montserrat', sans-serif;
    background-size: cover;
    opacity: 0.8;
}

.mainContainer {
    height: 90%;
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

.projectsContainer {
    display: flex;
    justify-content: space-around;
    width: 100%;
    flex-wrap: wrap;
}
